    public ngOnInit() {
        this.numResults = this.allRows.length * 2;
        this.setUpTableBody();
    }

    public onPageChanged(page: number) {
        this.currentPage = page;
        this.setUpTableBody();
    }

    private setUpTableBody() {
        this.tableConfigDark.body = [];
        if (this.currentPage > 5) {
            for (
                let i = (this.currentPage - 6) * this.pageSize;
                i < (this.currentPage - 6) * this.pageSize + this.pageSize;
                i++
            ) {
                this.tableConfigDark.body.push(this.allRows[i]);
            }
        } else {
            for (
                let i = (this.currentPage - 1) * this.pageSize;
                i < (this.currentPage - 1) * this.pageSize + this.pageSize;
                i++
            ) {
                this.tableConfigDark.body.push(this.allRows[i]);
            }
        }
    }
