import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DemoComponent } from './demo.component';
import { DemoTableComponent } from './demo-table-component/demo-table.component';
import { DemoPaginationComponent } from './demo-pagination-view/demo-pagination.component';
// import { ModalComponent } from './modal/modal.component';

const routes: Routes = [
    {
        path: '',
        component: DemoComponent,
        children: [
            {
                path: 'action',
                loadChildren: './action-view/action-view.module#ActionViewModule'
            },
            {
                path: 'contextMenu',
                // tslint:disable-next-line: max-line-length
                loadChildren: './context-menu-view/context-menu-view.module#ContextMenuViewModule'
            },
            {
                path: 'footer',
                loadChildren: './footer-view/footer.view.module#FooterViewModule'
            },
            {
                path: 'header',
                loadChildren: './header-view/header-view.module#HeaderViewModule'
            },
            {
                path: 'logoIcon',
                loadChildren: './logo-icon-view/logo-icon-view.module#LogoIconViewModule'
            },
            {
                path: 'styleGuide',
                loadChildren: './style-guide/style-guide.module#StyleGuideModule'
            },
            {
                path: 'tabs',
                loadChildren: './tabs-view/tabs-view.module#TabsViewModule'
            },
            {
                path: 'table',
                component: DemoTableComponent
            },
            {
                path: 'toast',
                loadChildren: './toast-view/toast-view.module#ToastViewModule'
            },
            {
                path: 'pagination',
                component: DemoPaginationComponent
            },
            {
                path: 'templateAction',
                // tslint:disable-next-line: max-line-length
                loadChildren:
                    './templateAction-view/templateAction-view.module#TemplateActionViewModule'
            },
            {
                path: 'widget',
                loadChildren: './widget-view/widget-view.module#WidgetViewModule'
            },
            {
                path: 'mastercard',
                loadChildren: './master-card-view/master-card-view.module#MasterCardViewModule'
            },
            {
                path: 'detail-card',
                loadChildren: './detail-card-view/detail-card-view.module#DetailCardViewModule'
            },
            {
                path: 'logs',
                loadChildren: './logs-view/logs-view.module#LogsViewModule'
            },
            // {
            //     path: 'modal',
            //     component: ModalComponent
            // },
            {
                path: '**',
                redirectTo: 'demoViewTemplate'
            }
        ]
    },
    { path: '**', redirectTo: '' }
];

@NgModule({
    exports: [RouterModule],
    imports: [RouterModule.forChild(routes)]
})
export class DemoViewRoutingModule {}
